import React from "react"
import { connectSearchBox } from "react-instantsearch-dom"
import styled from "@emotion/styled"
import search_icon from "../../images/search_icon.svg"
import {isMobile}  from "react-device-detect"
import color from "../color"

const SearchInput = styled.input`
  border: ${props => (props.isMobile ? `2px solid `+color.border : `none`)};
  border-bottom: ${props => (props.isMobile ? `2px solid`+color.border : `1px solid #5c5c5c`)} ;
  height: 30px;
  margin-right: 20px;
  padding: 0 0 0 30px;
  background-image: url(${search_icon});
  background-position: center left;
  background-size: 25px;
  background-repeat: no-repeat;
   &:focus {
    outline: none;
  }
`

function blurSubmit(e){
  e.preventDefault();
  e.stopPropagation();
}

const Form = styled.form`
  text-align: ${props => (props.isMobile ? `center` : `right`)};
`

function keypressHandler(e){
  if (e.key === "Enter") {
    e.target.blur();
  }
}

export default connectSearchBox(({ refine, ...rest }) => (
  <Form onSubmit={e => {blurSubmit(e)}} isMobile={isMobile}>
    <SearchInput
      isMobile={isMobile}
      size='27'
      type="text"
      placeholder="Search this blog"
      aria-label="Search this blog"
      onChange={e => refine(e.target.value)}
       onKeyDown = {e=> keypressHandler(e)}
      {...rest}
    />
  </Form>
))